module.exports = "body{\r\n    margin:0px;\r\n    padding:0px;\r\n    font-family:sans-serif;\r\n    background-size:cover;\r\n}\r\n.box{\r\n    position:absolute;\r\n    top:50%;\r\n    left:50%;\r\n    transform:translate(-50%,-50%);\r\n    /*实现块元素百分比下居中*/\r\n    width:450px;\r\n    padding:50px;\r\n    background: rgba(0,0,0,.8);\r\n    box-sizing:border-box;\r\n    box-shadow: 0px 15px 25px rgba(0,0,0,.5);\r\n    border-radius:15px;\r\n}\r\n.box h2{\r\n    margin:0 0 30px;\r\n    padding:0;\r\n    color: #fff;\r\n    text-align:center;\r\n}\r\n.box .inputbox{\r\n    position:relative;\r\n}\r\n.box .inputbox input{\r\n    width: 100%;\r\n    padding:10px 0;\r\n    font-size:16px;\r\n    color:#fff;\r\n    letter-spacing: 1px;\r\n    margin-bottom: 30px;\r\n    border:none;\r\n    border-bottom: 1px solid #fff;\r\n    outline:none;\r\n    background: transparent;\r\n}\r\n.box .inputbox label{\r\n    position:absolute;\r\n    top:0px;\r\n    left:0px;\r\n    padding:10px 0;\r\n    font-size: 16px;\r\n    color:#fff;\r\n    pointer-events:none;\r\n    transition:.5s;\r\n}\r\n.box .inputbox input:focus ~ label,\r\n.box .inputbox input:valid ~ label\r\n{\r\n    top:-18px;\r\n    left:0;\r\n    color:#03a9f4;\r\n    font-size:14px;\r\n}\r\n.box input[type=\"submit\"]\r\n{\r\n    background: transparent;\r\n    border:none;\r\n    outline:none;\r\n    font-size: 16px;\r\n    color:#fff;\r\n    background: #03a9f4;\r\n    padding:15px 20px;\r\n    cursor: pointer;\r\n    border-radius:10px;\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"